*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.App{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}
.tableContainer{
    width: 80%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x:auto;
}
.tableControls{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 2vh;
}

.controlRight{
    display: flex;
    flex-direction: row;
    justify-content: right;
    width: 50%;
}
.controlRight .controlParent{
    /* width:30%; */
    margin-left: 3%;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    border: 1px solid #ddd;
  }
  
  th, td {
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even){background-color: #f2f2f2}
  .paginationContainer{
      margin-top: 2vh;
      min-height: 5vh;
  }
  .paginationContainer button{
    display: inline-block;  
    width: 1.5rem;
    min-height: 2vh;
      box-shadow: 0px 0px 5px black;
      border-radius: 35%;
      margin: 0 0.5em;
      text-align: center;
      border: none;
      background-color: white

  }
  .paginationContainer button:hover{
      cursor: pointer;
      color: white;
      background-color: black;
  }
.paginationContainer .active{
    background-color: greenyellow;
}

@media only screen and (max-width: 600px) {
    .tableControls{
        flex-direction: column;
        align-items: center
    }
    .controlLeft{
        min-width: 60%;
        margin-left: 3%;
    }
    .controlRight{
        flex-direction: column;
        min-width: 60%;
    }
}